.header {
	padding-top: 20px;
	border-bottom: 2px solid #f9f9f9;
	
	&__inner {
		border-bottom: 2px solid gray;
		padding-bottom: 20px;
		
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	
}

.footer {
	padding-bottom: 20px;
	
	&__inner {		
		border-top: 2px solid gray;
		padding-top: 20px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	
}

.main_menu {
	
	&__list {
		
	}
	
	&__item {
		text-transform: lowercase;
		display: inline-block;
		vertical-align: middle;
		margin-left: 10px;
		
		a {
			padding: 5px 15px;
			font-weight: 600;
			text-transform: uppercase;
		}
	}
	
}
