.modal_underlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.4);
	z-index: 3;
	display: none;
}

.loader {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	z-index: 4;
	
	svg {
		position: absolute;
		left: 0;
		right: 0;
		margin: auto;
		top: 200px;
		transform-origin: center;
		width: 150px;
		height: 150px;
		
		@keyframes rotate {
			from { 
				transform: rotate(0deg);
			}
			to {
				transform: rotate(360deg);
			}
		}
		
		animation: rotate 1.5s infinite linear;
		
	}
}

.modal {
	background-color: #fff;
	width: 700px;
	position: fixed;
	left: 0;
	right: 0;
	margin: 0 auto;
	top: 100px;
	z-index: 4;
	
	color: $darkest;
	text-align: center;
	
	&__inner {
		padding: 40px;
	}
	
	&__line {
		margin-bottom: 20px;
	}
	
	&__title {
		text-transform: uppercase;
		font-weight: 600;
		text-align: center;
		font-size: 25px;
	}
	
	&__close {
		display: block;
		position: absolute;
		right: 20px;
		top: 20px;
		
		svg {
			width: 25px;
			height: 25px;
		}
	}
	
	&__text {
		text-align: left;
		margin-bottom: 5px;
	}
	
	&__form {
		width: 400px;
		margin: 0 auto;
		margin-top: 30px;
		margin-bottom: 20px;
	}
}

.modal_order {
	display: none;
}

.modal_success {
	display: none;
	width: 400px;
}

.loader {
	display: none;
}