.catalog {
	padding: 25px 0;
	overflow: hidden;
	
	&__sidebar {
		float: left;
		width: 30%;
		margin-right: 30px;
	}
	
	&__content {
		float: left;
		width: calc(70% - 30px);
	}
	
	&__head {
		overflow: hidden;
		border-bottom: 1px solid;
	}
	
	&__list {
		padding-top: 20px;
		font-size: 0;
	}
	
	&__item {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 20px;
		
		.catalog__list--two & {
			margin-right: 16px;
			width: calc(50% - 8px);
			
			&:nth-child(2n) {
				margin-right: 0;
			}
		}
		
		.catalog__list--three & {
			margin-right: 16px;
			width: calc((100% - 16px * 2) / 3);
			
			&:nth-child(3n) {
				margin-right: 0;
			}
		}
	}
	
}

.catalog_cart {
	display: inline-block;
	font-size: 16px;
	position: relative;
	color: $base;
	
	&:hover {
		color: $base;
	}
	
	
	&--special {
		border: 2px solid $primary;
	}
	
	&--disabled {
		opacity: 0.5;
		cursor: default;
	}
	
	&__image {
		width: 100%;
		position: relative;
		font-size: 0;
	}
	
	&__content {
		display: block;
		width: 100%;
		padding-left: 10px;
		padding-top: 4px;
	}
	
	&__title {
		margin-bottom: 5px;
		font-size: 18px;
		line-height: 22px;
		height: 44px;
		overflow: hidden;
	}
	
	&__discount {
		position: absolute;
		width: 45px;
		height: 35px;
		top: 0;
		left: 20px;
		color: #fff;
		background-color: $primary;
		font-size: 18px;
		font-weight: 600;
		line-height: 35px;
		text-align: center;
	}
	
	&__footer {
		overflow: hidden;
		width: 100%;
	}
	
	&__price {
		float: left;
		width: 55%;
		line-height: 25px;
		
		
		&_old {
			display: inline-block;
			text-decoration: line-through;
			margin-right: 15px;
			opacity: 0.5;
		}
		
		&_new {
			display: inline-block;
			font-weight: 600;
		}
		
	}
	
	&__btn {
		float: right;
		width: 45%;
		text-align: right;
	}
	
	&__timer {
		display: none;
		position: absolute;
		width: 100%;
		bottom: 0;
		left: 0;
		
		.catalog_cart--special & {
			display: block;
		}
	}
}

.catalog_basket, 
.catalog_filters,
.catalog__content {
	padding: 10px;
	background-color: #fff;
}

.catalog_view {
	float: left;
	width: 200px;
	text-align: right;
	
	&__item {
		display: inline-block;
		vertical-align: top;
		margin-right: 10px;
		cursor: pointer;
		
		&:last-child {
			margin-right: 0;	
		}
		
		svg {
			transition: fill .3s ease;
			width: 23px;
			height: 23px;
			fill: #b5c1c4;
		}
	}
	
	&__item--active {
		
		svg {
			fill: $primary;
		}
		
	}
}

.catalog_sort {
	overflow: hidden;
	float: left;
	width: calc(100% - 200px);
	
	&__text {
		font-size: 18px;
		text-transform: uppercase;
		float: left;
		width: 150px;
		line-height: 27px;
	}	
	
	&__item {
		display: inline-block;
		vertical-align: middle;
		padding: 2px 4px;
		margin-right: 10px;
		
		&:last-child {
			margin-right: 0;
		}
	}

}


.catalog_basket {
	margin-bottom: 30px;

	&__title {
		border-bottom: 1px solid;
		text-transform: uppercase;
		font-size: 18px;
		line-height: 27px;
	}
	
	&__list {
		border-bottom: 1px solid;
		padding: 12px 0;
		
	}
	
	&__line {
		margin-bottom: 9px;
		overflow: hidden;
	}
	
	&__product {
		padding-left: 18px;
		position: relative;
		line-height: 20px;
		float: left;
		width: 65%;
		padding-right: 10px;
		
		&::before {
			width: 18px;
			height: 20px;
			position: absolute;
			left: 0;
			top: 0;
		}
		
		&:last-child {
			margin-bottom: 0;
		}
	}
	
	@for $i from 1 through 20 {
		&__line:nth-child(#{$i}) &__product::before {
			content: '#{$i}.';
		}
	}
	
	&__price {
		float: left;
		width: 25%;
	}
	
	&__close {
		width: 10%;
		float: left;
		text-align: right;
		cursor: pointer;
		
		svg {
			display: inline-block;
			width: 20px;
			height: 20px;
		}
	}
	
	&__summ {
		overflow: hidden;
		padding: 10px 0;
	}
	
	&__summ_text {
		width: 35%;
		float: right;
		font-weight: 600;
	}
}


.price {
	white-space: nowrap;
	
	&::before {
		content: '$ ';
		font-size: inherit;
	}
}

.btn {
	display: inline-block;
	background-color: $primary;
	color: #fff;
	text-transform: uppercase;
	padding: 3px 10px;
	transition: all .3s ease;
	
	&:hover {
		background-color: lighten($primary, 10%);
		color: #fff;
	}
}

.timer {
	font-size: 0;
	background-color: rgba(0,0,0,0.6);
	padding: 3px 0;
	
	&__item {
		display: inline-block;
		vertical-align: middle;
		width: 25%;
		
		span {
			display: block;
			text-align: center;
			color: #fff;
		}
		
		span:first-child {
			font-size: 20px;
			font-weight: 600;
		}
		
		span:last-child {
			text-transform: uppercase;
			font-size: 15px;
		}
		
	}
}

.catalog_filters {
	
	
	&__list {
		
	}
	
	&__item:first-child .filter__title {
		border-top: 0;
	}
	
}


.filter {
	
	&__title {
		// border-bottom: 1px solid;
		cursor: pointer;
		border-top: 2px solid;
		text-transform: uppercase;
		line-height: 50px;
		padding-left: 10px;
		position: relative;
		
		&::after {
			content: '';
			display: block;
			width: 10px;
			height: 10px;
			border-bottom: 1px solid;
			border-right: 1px solid;
			transform: rotate(45deg);
			position: absolute;
			top: 0;
			bottom: 0;
			margin: auto 0;
			right: 10px;
			transition: transform .2s ease;
			
			.filter--open & {
				transform: rotate(-135deg);
			}
		}
		
	}
	
	&__content {
		padding-top: 10px;
		padding-bottom: 20px;
		padding-left: 10px;
		overflow: hidden;
		// background-color: cornsilk;
		
		display: none;
		
		.filter--open & {
			display: block;
		}
	}
	
	&__item {
		display: block;
		margin-bottom: 8px;
		
		&:last-child {
			margin-bottom: 0;
		}
	}
	
	&__input,
	&__text {
		margin-bottom: 10px;
	}
	
	&__text {
		line-height: 27px;
	}
	
	&__column {
		float: left;
		width: 30%;
		padding-right: 10px;
	}
	
}

.input {
	border: 1px solid $base;
	width: 100%;
	padding: 3px 5px;
	outline: none;
	
	&:focus {
		border: 1px solid $primary;
	}
	
	&[type="date"] {
		width: 120px;
	}
	
	&--error {
		border: 2px solid red;
	}
}