::selection {
	background: $primary;
}

html, 
body {
	min-height: 100%;
}

body {
	min-width: 320px;
	color: $base;
	background: $light;
	font-family: 'Raleway', sans-serif;
	font-weight: 400;
	font-size: 16px;
	cursor: default;
	background-color: #f9f9f9;
}

.wrap {
	margin: 0 auto;
	padding: 0 10px;

	width: 100%;
	min-width: 1100px;
	max-width: 1440px;
}

.logo {
	font-size: 32px;
	text-transform: uppercase;
	font-weight: 600;
	color: $darkest;
	
	&:hover {
		color: $darkest;
	}
	
	svg {
		width: 50px;
		height: 50px;
	}
	
	a, svg {
		display: inline-block;
		vertical-align: middle;
	}
}

a {
	color: $darkest;
	transition: color .3s ease;
	
	&:hover {
		color: $primary;
	}
}


.epixx {
	color: $primary;
}


.promo {
	text-align: center;
	padding: 30px 0;

	
	background-color: $primary;
	color: #fff;
	
	&__title {
		font-size: 40px;
		text-transform: uppercase;
		margin-bottom: 10px;
	}
	
	&__subtitle {
		margin-bottom: 50px;
	}
	
	&__note {
		
		span,
		svg {
			display: inline-block;
			vertical-align: middle;
		}
		
		svg {
			width: 18px;
			height: 20px;
			margin-left: 10px;
		}
	}
	
}

.checkbox {
	cursor: pointer;
	display: inline-block;
	overflow: hidden;
	
	&__input {
		display: none;
	}
	
	&__input:checked + .checkbox__box {

		&::before {
			background-color: $primary;	
		}

	}

	&__box {
		float: left;
		display: block;
		margin-right: 10px;
		width: 16px;
		height: 22px;
		position: relative;
		
		&::before {
			content: '';
			display: block;
			width: 16px;
			height: 16px;
			border: 1px solid $base;
			position: absolute;
			top: 0;
			bottom: 0;
			margin: auto 0;
			left: 0;
			transition: opacity .3s ease;
			background-color: #fff;
		}
	}

			
	&__label {
		float: left;
		width: calc(100% - 32px);
		font-size: 14px;
		line-height: 22px;
		color: $base;
		
		span {
			text-decoration: underline;
		}
			
	}
			
}